footer {
	width: 100%;
	border-top: 1px solid #bbb;
	.inner {
		width: 1180px;
		margin: 0 auto;
		position: relative;
		padding: 50px;
		p {
			font: 12px/1.2 'arial';
			color: #999;
			text-align: center;
			letter-spacing: 2px;
		}
		.members {
			img {
				width: 50px;
			}
		}
	}
}
//tablet
@media screen and (max-width: $tablet) {
	footer {
		.inner {
			width: 100%;
		}
	}
}
